"use client"

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { ArrowLeft, Send, Sparkles } from "lucide-react"
import Image from "next/image"
import { useState } from "react"

export default function ChatPage() {
  const [message, setMessage] = useState("")
  const [messages, setMessages] = useState([
    {
      role: "assistant",
      content: "안녕하세요! Next Me AI 어시스턴트입니다. 재무 관련 궁금한 점을 물어보세요.",
    },
  ])

  const handleSend = () => {
    if (!message.trim()) return

    setMessages([...messages, { role: "user", content: message }])
    setMessage("")

    // Simulate AI response
    setTimeout(() => {
      setMessages((prev) => [
        ...prev,
        {
          role: "assistant",
          content: "질문 감사합니다. 현재 데모 모드로 실제 AI 응답은 백엔드 연동 후 가능합니다.",
        },
      ])
    }, 1000)
  }

  return (
    <div className="min-h-screen flex flex-col">
      {/* Header */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Link href="/dashboard">
              <Button variant="ghost" size="icon">
                <ArrowLeft className="h-5 w-5" />
              </Button>
            </Link>
            <div className="flex items-center gap-2">
              <div className="h-8 w-8 rounded-full bg-primary flex items-center justify-center">
                <Sparkles className="h-4 w-4 text-primary-foreground" />
              </div>
              <div>
                <p className="font-semibold text-sm">AI 어시스턴트</p>
                <p className="text-xs text-muted-foreground">온라인</p>
              </div>
            </div>
          </div>
          <Link href="/" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={100} height={27} className="h-7 w-auto" />
          </Link>
        </div>
      </header>

      {/* Chat Messages */}
      <div className="flex-1 overflow-y-auto">
        <div className="container mx-auto px-4 py-8 max-w-4xl">
          <div className="space-y-6">
            {messages.map((msg, idx) => (
              <div key={idx} className={`flex gap-4 ${msg.role === "user" ? "justify-end" : "justify-start"}`}>
                {msg.role === "assistant" && (
                  <div className="h-10 w-10 rounded-full bg-primary flex items-center justify-center flex-shrink-0">
                    <Sparkles className="h-5 w-5 text-primary-foreground" />
                  </div>
                )}
                <Card className={`p-4 max-w-[70%] ${msg.role === "user" ? "bg-primary text-primary-foreground" : ""}`}>
                  <p className="text-sm">{msg.content}</p>
                </Card>
                {msg.role === "user" && (
                  <Avatar className="flex-shrink-0">
                    <AvatarFallback>김</AvatarFallback>
                  </Avatar>
                )}
              </div>
            ))}
          </div>

          {/* Suggested Questions */}
          {messages.length === 1 && (
            <div className="mt-8 space-y-4">
              <p className="text-sm text-muted-foreground text-center">자주 묻는 질문</p>
              <div className="grid md:grid-cols-2 gap-3">
                <Button
                  variant="outline"
                  className="h-auto p-4 text-left justify-start bg-transparent"
                  onClick={() => setMessage("주택 구매를 위해 얼마나 저축해야 하나요?")}
                >
                  <span className="text-sm">주택 구매를 위해 얼마나 저축해야 하나요?</span>
                </Button>
                <Button
                  variant="outline"
                  className="h-auto p-4 text-left justify-start bg-transparent"
                  onClick={() => setMessage("은퇴 준비는 언제부터 시작하는 게 좋을까요?")}
                >
                  <span className="text-sm">은퇴 준비는 언제부터 시작하는 게 좋을까요?</span>
                </Button>
                <Button
                  variant="outline"
                  className="h-auto p-4 text-left justify-start bg-transparent"
                  onClick={() => setMessage("지출을 줄일 수 있는 방법이 있나요?")}
                >
                  <span className="text-sm">지출을 줄일 수 있는 방법이 있나요?</span>
                </Button>
                <Button
                  variant="outline"
                  className="h-auto p-4 text-left justify-start bg-transparent"
                  onClick={() => setMessage("투자는 어떻게 시작하면 좋을까요?")}
                >
                  <span className="text-sm">투자는 어떻게 시작하면 좋을까요?</span>
                </Button>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Input Area */}
      <div className="border-t border-border bg-card/50 backdrop-blur-sm">
        <div className="container mx-auto px-4 py-4 max-w-4xl">
          <form
            onSubmit={(e) => {
              e.preventDefault()
              handleSend()
            }}
            className="flex gap-2"
          >
            <Input
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              placeholder="메시지를 입력하세요..."
              className="flex-1 bg-background"
            />
            <Button type="submit" size="icon">
              <Send className="h-4 w-4" />
            </Button>
          </form>
        </div>
      </div>
    </div>
  )
}
